import React, { Component } from "react";
import axios from 'axios';
import CryptoJS from "crypto-js";



export default class LogIn extends Component {

  constructor(props) {
    super(props);
    this.state = {
      email: "",
      password: "",
    };
  }
componentDidMount()
{
    this.passField = document.getElementById("loginPasswordfield");
    this.emailField = document.getElementById("loginEmailField");
    this.emailField.focus();
}

  onSubmit = (e) => {
    e.preventDefault()
    axios.get(`https://qacvmanager.azurewebsites.net/api/login/${this.state.email}&${CryptoJS.MD5(this.state.passwordInput).toString()}`)
      .then(
        response => {
          //console.log(response.data[0])
          if (response.status === 200) {
            //console.log("Login Sucessful");
            this.props.loginInfo.push(response.data[0].userId);
            this.props.loginInfo.push(response.data[0].firstName);
            this.props.loginInfo.push(response.data[0].lastName);
            this.props.loginInfo.push(response.data[0].email);
            this.props.loginInfo.push(response.data[0].department.departmentId);
            this.props.loginInfo.push(response.data[0].department.role);
            //console.log(this.props.loginInfo[4]);

            this.props.onSubmit(this.props.loginInfo);
            window.location.replace("#/user");
          }
        })
      .catch((error) => {
        window.alert("Invalid Details");
        this.passField.value = "";
        this.emailField.value = "";
         this.emailField.focus();
      })
  }
  getEmail = (e) => {
    this.setState({
      [e.target.name]: e.target.value
    })
  }
  hashPass = (e) => {
    e.preventDefault();
    this.setState({
      [e.target.name]: e.target.value
      //[e.target.name]: CryptoJS.MD5(e.target.value).toString()
    })
  }
  render() {
    return (
      <div id="login-page">
        <div class="limiter">
          <div className="container-login100">
            <div className="wrap-login100 p-l-85 p-r-85 p-t-55 p-b-55">
              <form className="login100-form validate-form flex-sb flex-w">
                <span className="login100-form-title p-b-32">
                  Account Login
                    </span>
                <span id="loginEmailLabel" className="txt1 p-b-11">
                  Username
                    </span>

                <div className="wrap-input100 validate-input m-b-36" data-validate="Username is required">
                  <input id="loginEmailField" className="input100" type="text" name="email" value={this.state.email}
                    onChange={e => this.getEmail(e)}></input>
                  <span className="focus-input100"></span>
                </div>

                <span id="loginPasswordLabel" className="txt1 p-b-11">
                  Password
            </span>

                <div className="wrap-input100 validate-input m-b-12" data-validate="Password is required">
                  <span className="btn-show-pass">
                    <i className="fa fa-eye"></i>
                  </span>

                  <input id="loginPasswordfield" className="input100" type="password" name="passwordInput" value={this.state.passwordInput}
                    onChange={e => this.hashPass(e)}></input>
                  <span className="focus-input100"></span>
                </div>

                <div class="flex-sb-m w-full p-b-48">
                  <div class="contact100-form-checkbox">
                    <input class="input-checkbox100" id="ckb1" type="checkbox" name="remember-me"></input>
                    <label class="label-checkbox100" for="ckb1">
                      Remember me
							</label>
                  </div>

                  <div>
                    <a href="#" class="txt3">
                      Forgot Password?
							     </a>
                  </div>
                </div>

                <div className="container-login100-form-btn">
                  <button id="btnLogin" className="login100-form-btn" onClick={e => this.onSubmit(e)}>
                    Login
						</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}


